@import "_palette";

.container {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: center;
  padding: 32px 0px;
}

.body_wrapper {
  align-self: center;
  flex-direction: column;
  display: flex;
  align-items: center;
  width: 100%;
  max-width: 1024px;
}

.infomation__wrapper {
  display: flex;
  flex-direction: row;
  align-self: stretch;
  padding-top: 1rem;

  @media screen and (max-width: 475px) {
    flex-wrap: wrap;
  }
}

.information__section {
  display: flex;
  flex: 1;
  flex-direction: column;
  padding: 1rem 2rem;
  @media screen and (max-width: 475px) {
    flex: 1;
    width: 100%;
  }
}
