@import "_palette";

.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}

.exp__list {
  padding-bottom: 1rem;
}

.exp__list_item {
  list-style: none;
  position: relative;
  padding: 0;
  padding-left: 32px;

  &::before {
    content: " ";
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background: $text;
    position: absolute;
    left: -15px;
  }
  &:not(:last-child) {
    padding-bottom: 30px;
    &::after {
      content: "  ";
      height: 100%;
      width: 5px;
      background: $text;
      position: absolute;
      bottom: 0%;
      left: -10px;
    }
  }
}

.exp__position {
  font-size: 0.9rem;
  color: $text;
  text-transform: uppercase;
  font-weight: bold;
}

.exp__company {
  font-size: 0.9rem;
  color: $primary;
  padding-top: 0.25rem;
  text-transform: uppercase;
}

.exp__job {
  font-size: 0.9rem;
  color: $text;
  padding-top: 0.25rem;
}
