@import "_palette";

.contact__item_wrapper {
  padding: 0.5rem 0;
  display: flex;
  justify-content: center;
  font-size: 1rem;
  align-items: center;
  @media screen and (max-width: 475px) {
    min-height: 54px;
    flex-wrap: wrap;
  }
}

.tag {
  padding: 4px 16px;
  border-radius: 25px;
  background: $text;
  color: white;
  margin-right: 2rem;
  margin-left: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  font-size: 0.7rem;
  min-width: 60px;
}
