@import "_palette";

.container {
  display: flex;
  flex-direction: row;
  margin: 1rem;
  position: relative;

  img {
    width: 40px;
    height: 40px;
    object-fit: contain;
    padding: 14px;
    border-radius: 50%;
    background: $text;
  }

  div {
    position: absolute;
    right: -40px;
    top: 50%;
    transform: translateY(-50%);
    padding: 4px;
    font-size: 14px;
    font-weight: bold;
    color: white;
    border-radius: 5px;
  }
}
