@import "_palette";

.wrapper {
  display: flex;
  align-self: center;
  flex-direction: column;
  @media screen and (max-width: 475px) {
    justify-content: flex-start;
    align-items: center;
  }
}

.name {
  font-size: 2rem;
  margin-bottom: 1rem;
  text-transform: uppercase;
  font-weight: 600;
  color: $text;
  @media screen and (max-width: 475px) {
    text-align: center;
  }
}

.description {
  font-size: 1rem;
  text-transform: uppercase;
  color: $primary;
}
